import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { INode } from "../shared/INode";
import { CdkDragEnd } from "@angular/cdk/drag-drop";

@Component({
  selector: "app-node",
  templateUrl: "./node.component.html",
  styleUrls: ["./node.component.scss"]
})
export class NodeComponent implements OnInit {
  @Input() node: INode;
  @Output() change: EventEmitter<INode> = new EventEmitter<INode>();
  @Output() editorExpand: EventEmitter<INode> = new EventEmitter<INode>();

  position: any;
  constructor() {}

  ngOnInit() {
    this.position = { x: this.node.x, y: this.node.y };
  }

  public isEditorExpanded: boolean = false;

  public onDragEnded(event: CdkDragEnd): void {
    console.log(event.source.getFreeDragPosition());
    let position = event.source.getFreeDragPosition();
    this.node.x = position.x;
    this.node.y = position.y;
    // returns { x: 0, y: 0 }
  }

  public addChildNode() {
    let node = {
      text: "new node",
      title: "new node",
      childNodes: [],
      x: 0,
      y: 0
    };

    this.node.childNodes.push(node);
    this.change.emit(node);
    console.log(this.node.x + " " + this.node.y);
  }

  public expandEditor() {
    this.editorExpand.emit(this.node);
  }
}
