import { Component, OnInit, ViewChild, Input, Output, EventEmitter } from "@angular/core";
import { INode } from "../shared/INode";

@Component({
  selector: "app-node-list",
  templateUrl: "./node-list.component.html",
  styleUrls: ["./node-list.component.scss"]
})
export class NodeListComponent implements OnInit {

  @Output() editorExpand: EventEmitter<INode> = new EventEmitter<INode>();

  public nodeList: INode[] = [
    { text: "some text", title: "some title", childNodes: [], x: 0, y: 0 }
  ];
  constructor() {}

  ngOnInit() {}

  addChildNode(node: INode) {
    this.nodeList.push(node);
  }

  editorExpandEventHandler(node: INode)
  {
    this.editorExpand.emit(node);
  }
}
